.drawer {
  width: 485px;
}

.drawer .drawerPaper {
  width: 485px;
  margin: auto;
  background-color: #0f0f0f;
  text-align: justify;
  align-items: left;
  justify-content: left; 
  box-shadow: 0.5px 0px 11px 0px rgba(0,0,0, 1), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 3px 3px -2px rgba(0,0,0,0.12);
}

