.infoGrid .bigAvatar {
    margin: 10px;
    width: 150px;
    height: 150px;
    border: 3px solid rgba(232, 232, 232, 0.8);
    box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.75) inset;
    -moz-box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.75) inset;
    -webkit-box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.75) inset;
}

.infoGrid .name {
    text-align: center;
    font-size:30px;
    color: white
}

.infoGrid .position {
    text-align: center;
    margin-top: 5px;
    color: white;
    font-style: italic;
}

.infoGrid .introduction {
    color: white;
    /*margin: 15px 70px;*/
    margin-left:14% ;
    margin-right: 7%;
    word-spacing: 2px;
    text-align: justify;
    align-items: center;
    font-size:24px;
    justify-content: center;
    font-stretch: condensed;
}

.infoGrid .divider {
    margin: 0;
    border: none;
    flex-shrink: 0;
    background-color: white;
    width: 320px;
}

.infoGrid .infoList {
    /*margin: 10px 114px;*/
    margin-left: 12%;
    margin-right: 30%;
    font-size:24px;
    color: white;
}

.infoGrid .socialMedias {
    text-align: center;
    margin-top: 25px;
}

.infoGrid .socialMediasIcon {
    margin: 0 20px;
}

.infoList span {
    color: white;
}

.infoList .infoIcon {
    color: #3f51b5;
    background-color: white;
}